import modalComponent from '@/components/modalComponent.vue'
import bannerComponent from '@/components/bannerComponent.vue';
import btnComponent from '@/components/btnComponent.vue';
import listItemComponent from '@/components/listItemComponent.vue';
export default {
	components: {
		bannerComponent,
		btnComponent,
		listItemComponent,
		modalComponent,
	},
	data() {
		return {
			modal: 'indexBanner',
			showBottomLoading: true,
			requestCount: 1,

			cutOffDate: '', // 截止日期
			searchHide: false,
			currentTab: 0,

			page: 1,
			keywords: '',
			rankField: 'week_hot',
			sign: 0,
			rankList: this.$app.getData('index_rankList') || [],
		};
	},

	onLoad(option) {
		// 显示精灵tab小红点
		uni.showTabBarRedDot({
			index: 1
		})



		// 跳转到明星页
		option.starid && this.goGroup(option.starid)
		this.getSunday()
	},
	onShow() {
		this.page = 1
		this.keywords = ''
		this.getRankList()
	},
	onShareAppMessage() {
		return this.$app.commonShareAppMessage()
	},
	onHide() {
		uni.pageScrollTo({
			scrollTop: 0,
			duration: 0,
		})
	},
	/**
	 * 下拉刷新
	 */
	onPullDownRefresh() {
		this.page = 1
		this.keywords = ''
		this.getRankList()
	},
	/**
	 * 上拉加载
	 */
	onReachBottom() {
		this.page++
		this.getRankList()

	},
	methods: {
		preimg(img) {
			uni.previewImage({
				current:0,
				urls: [img.trim()],
				success:res=>{
					console.log(res);
				}
			})
		},
		getSunday() {
			const time = new Date()
			const day = time.getDay() || 7
			time.setDate(time.getDate() - day + 7)
			this.cutOffDate = (time.getMonth() + 1) + '月' + time.getDate() + '日'
		},
		getLast() {
			var date = new Date();
			var currentMonth = date.getMonth();
			var nextMonth = ++currentMonth;
			var nextMonthFirstDay = new Date(date.getFullYear(), nextMonth, 1);
			var oneDay = 1000 * 60 * 60 * 24;
			let dateTime = new Date(nextMonthFirstDay - oneDay);

			this.cutOffDate = dateTime.getMonth() + 1 + '月' + dateTime.getDate() + '日'
		},
		/**
		 * 去偶像圈打榜
		 */
		goGroup(starid) {





			if (this.$app.getData('userStar', true)['id'] == starid) {
				this.$app.goPage('/pages/group/group')
			} else {
				this.$app.goPage('/pages/star/star?starid=' + starid)
			}
			return
		},
		/**
		 * 其他榜单（韩星榜）
		 */
		changeSign(sign) {
			this.page = 1
			this.sign = sign
			this.keywords = ''
			this.getRankList()
		},
		changeField(field) {
			this.page = 1
			this.rankField = field
			this.keywords = ''
			this.getRankList()
		},
		searchInput(e) {
			if (!this.keywords || !e.detail.value) this.rankList = []
			this.page = 1
			this.sign = 0
			this.keywords = e.detail.value
			this.getRankList()
		},
		searchToggle() {
			this.searchHide = !this.searchHide
			if (this.keywords) {
				this.keywords = ''
				this.page = 1
				this.sign = 0
				this.getRankList()
			}
		},

		// HTTP
		getRankList() {
			this.showBottomLoading = true
			this.$app.request(this.$app.API.STAR_RANK, {
				page: this.page,
				rankField: this.rankField,
				keywords: this.keywords,
				sign: this.sign,
			}, res => {
				if (res.data.length < 10) {
					// 内容不足隐藏loadicon
					this.showBottomLoading = false
				}

				const rankList = []
				res.data.forEach(e => {
					const item = {}
					item.starid = e.star.id
					item.name = e.star.name
					item.avatar = e.star.head_img_s ? e.star.head_img_s : e.star.head_img_l
					item.hot = this.$app.formatNumberRgx(e[this.rankField])
					rankList.push(item)
				})
				if (this.page == 1) {
					this.rankList = rankList
					this.$app.setData('index_rankList', this.rankList)

				} else {
					// 追加data
					this.rankList = this.rankList.concat(rankList)
				}

				this.$app.closeLoading(this)
			})

		},
	}
}
